@import url("https://fonts.googleapis.com/css?family=Roboto");
/* miriam-libre-regular - latin */
@font-face {
  font-family: "Miriam Libre";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/miriam-libre-v7-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Miriam Libre Regular"), local("MiriamLibre-Regular"),
    url("../fonts/miriam-libre-v7-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/miriam-libre-v7-latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/miriam-libre-v7-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("../fonts/miriam-libre-v7-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/miriam-libre-v7-latin-regular.svg#MiriamLibre")
      format("svg"); /* Legacy iOS */
}
