.work__container--gallery {
    display: grid;
    position: absolute;
    overflow: hidden;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    grid-template-columns: 60% 40%;
    grid-template-rows: 1fr; }
.gallery__h1 {
    font-size: 3.2em;
    &--light {
        color: white; }
    &--dark {
        color: #1F1F31; } }
.gallery__p {
    font-size: 1.2em;
    font-weight: 200;
    &--light {
        color: white; }
    &--dark {
        color: #1F1F31; } }
.work__gallery {
    grid-column: 1 / 2;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
        max-height: 90vh;
        max-width: 80%;
        object-fit: contain; } }
.work__article {
    position: relative;
    padding: 1.6em;
    grid-column: 2 / 3; }
.gallery__triangle {
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-top: 7em solid white;
    border-left: 8em solid transparent;
    z-index: 0; }

.close {
    position: absolute;
    right: 10px;
    top: -100px;
    width: 32px;
    height: 32px;
    opacity: 0.3; }

.close:hover {
    opacity: 1; }

.close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #333; }

.close:before {
    transform: rotate(45deg); }

.close:after {
    transform: rotate(-45deg); }

.example-enter {
    opacity: 0.01; }

.example-enter.example-enter-active {
    opacity: 1;
    transition: opacity 200ms ease-in; }

.example-leave {
    opacity: 1;
    &.example-leave-active {
        opacity: 0.01;
        transition: opacity 200ms ease-in; } }
.example-appear {
    opacity: 0.01;
    &.example-appear-active {
        opacity: 1;
        transition: opacity .2s ease-in; } }
